import { mapState } from 'vuex';
// import $ from 'jquery';
import indexcoupon from '@/components/coupon/indexcoupon.vue';
import loadGif from '@/components/loadGif/loadGif.vue';
export default {
    data() {
        return {
            searchWord: '',  // 搜索用的参数
            couponType: 0, // 搜索用 优惠券类型 默认为0 全部搜索
            page: 1, // 页数 查询用
            searchCouponList: [],
            show: false,
            maskShow: false,  // 判断 遮罩层 用
            type1: '券类型',
            busy: true,
            nextPage: 0,  // 判断用
            currentPage: 0,
            iconShow: false,
            headInput: true,
            showLoadGif: true,
            couponTypeList: [
                {
                    name: '全部',
                    value: 0
                },
                {
                    name: 'N元购',
                    value: 1
                },
                {
                    name: '抵扣券',
                    value: 3
                },
                {
                    name: '折扣券',
                    value: 4
                },
                {
                    name: '实物券',
                    value: 5
                },
                {
                    name: '体验券',
                    value: 6
                },
                {
                    name: '兑换券',
                    value: 7
                },
                {
                    name: '代金券',
                    value: 8
                }
            ]
        };
    },
    computed: {
        ...mapState(['localCity', 'global'])
    },
    beforeCreate() {
    },
    watch: {
        // 监听search的变化
        searchWord: function(newval, oldval) {
            this.$data.page = 1; // 发现search变化后 将page变为1
        }
    },
    methods: {
        // ListIsNull() {
        //     if (this.searchCouponList.length < 1) {
        //         this.showLoadGif = false;
        //         this.$vux.toast.text('当前没有优惠券', 'bottom');
        //     }
        // },
        hiddenMask() {  //  收起下拉框和遮罩层
            this.show = false;
            this.maskShow = false;
            this.iconShow = false;
        },
        typeSearch(couponType1) {
            this.couponType = couponType1.value;
            this.page = 1; // 进入类型搜索时将page设为1
            this.searchCoupon();
            this.type1 = couponType1.name;
            this.show = false;
            this.maskShow = false;
        },
        showCouponType() {
            if (this.show === true) {
                this.show = false;
                this.maskShow = false;
                this.iconShow = false;
            } else {
                this.show = true;
                this.maskShow = true;
                this.iconShow = true;
            }
        },
        goBack () {
            this.$router.go(-1);
        },
        searchCoupon() {  // 搜索用的方法
            let that = this;
            this.showLoadGif = true;
            this.axios({
                method: 'post',
                url: this.$store.state.global.hftcomClient,
                data: '{"id":19,"jsonrpc":"2.0","method":"listCoupon","params":{"couponType":' + this.$data.couponType + ',"searchWord":"' + this.$data.searchWord + '","longitude":' + this.$store.state.longitude + ',"latitude":' + this.$store.state.latitude + ',"page":' + this.page + ',"city":"' + this.$store.state.localCity + '","userCode":"' + this.$store.state.userCode + '"}}',
                contentType: 'application/json',
                dataType: 'json'
            }).then((result) => {
                if (that.page === 1) {
                    that.$data.searchCouponList = result.data.result.couponList.CollectInfo;
                } else {
                    that.$data.searchCouponList = [...that.$data.searchCouponList, ...result.data.result.couponList.CollectInfo];
                }
                that.nextPage = result.data.result.nextPage;
                that.currentPage = result.data.result.page;
                that.page++;
                that.busy = false;
                // that.ListIsNull();
                that.hiddenLoadGif();
            });
        },
        searchConponLoadMore () {
            this.busy = true;
            if (this.nextPage > this.currentPage) {
                this.searchCoupon();
            } else {
                this.busy = true;
                this.$vux.toast.text('加载完毕', 'bottom');
                this.showLoadGif = false;
            }
        },
        hiddenLoadGif() {
            if (this.nextPage <= this.currentPage) {
                this.$vux.toast.text('加载完毕', 'bottom');
                this.showLoadGif = false;
            }
        }
    },

    created() {
        this.searchCoupon();
    },
    mounted() {
        this.headInput = false;
    },
    components: {
        indexcoupon,
        loadGif
    }
};



